<script>
import { Swipe, SwipeItem, Dialog } from 'vant'
import swipeList, { originalConfig, codeMap } from './config'
import { getSearch } from '@/utils'
import Statement from '@/components/Statement'
import wxAuth from '@/helper/Auth/wxAuth'
import { mapState } from 'vuex'
import CountTo199 from '@/components/common/CountTo199'
export default {
  components: {
    VanSwipe: Swipe,
    VanSwipeItem: SwipeItem,
    Statement,
    CountTo199
  },
  data () {
    return {
      stateIsShow: getSearch('medId') === '1610', // 1610 快手渠道
      stateModelShow: false, // 声明弹窗 快手渠道需要显示此弹窗
      ...originalConfig, // 埋点数据
      swipeList, // 轮播数组
      phone: '', // 手机号
      code: '', // 验证码
      paytype: 1 // 支付类型
    }
  },
  async beforeRouteEnter (to, from, next) {
    // 获取微信授权
    await wxAuth({ getTokenQuery: { ...getSearch() } })
    next()
  },
  computed: {
    ...mapState('user', ['userInfo'])
  },
  async created () {
    const { id, unionid, wx_body: { openid } } = this.userInfo
    this.$log.update({
      openid,
      unionid,
      uid: id,
      mid: id
    })
    this.checkOrderInWx()
  },
  methods: {
    checkOrderInWx () {
      return this.$apis.payment.checkOrderInWx({ product_sn: 'PRODUCT_SN_XIAOBAI_0' }).catch(err => {
        this.payErrorBack(err)
        return Promise.reject(err)
      })
    },
    async onZeroSubmitPay (model = {}) {
      this.$log.click({
        current_page: location.href,
        module_ori: '表单提交',
        module_part: '106_toufang_lcx_0379'
      })
      this.$apis.userOperator.report()
      const { successBack = this.paySuccessBack, errorBack = this.payErrorBack } = model
      try {
        const { order_num: orderNum, product_sn: productSn } = await this.$apis.payment.payInWx({
          product_sn: 'PRODUCT_SN_XIAOBAI_0',
          ...getSearch()
        })
        // 成功回调
        typeof successBack === 'function' && successBack()
        this.hrefToSuccess(productSn, orderNum)
      } catch (error) {
        typeof errorBack === 'function' && errorBack(error)
      }
    },
    // 支付成功
    hrefToSuccess (productSn, orderNum) {
      this.$router.push({
        name: 'QrcodeInWx'
      })
    },
    // 支付成功回调
    paySuccessBack () {
      this.$log.click({
        current_page: location.href,
        module_ori: '表单提交',
        module_part: this.SUBMIT_BTN
      })
    },
    // 支付失败
    payErrorBack (data) {
      console.log(data)
      if (data.code === 30009) {
        Dialog({
          message: data.msg,
          confirmButtonText: '查看老师'
        }).then(() => {
          this.hrefToSuccess()
          this.$log.click(codeMap[data.code].report)
        })
        return
      }
      if (codeMap[data.code]) {
        Dialog({ message: data.msg || codeMap[data.code].msg }).then(() => {
          this.$log.click(codeMap[data.code].report)
        })
        return
      }
      Dialog({ message: data.msg })
    },
    // 成功获取验证码
    getCodeSuccessBack () {

    },
    // 获取验证码失败
    getCodeErrorBack () {

    }
  }
}
</script>
