<template>
  <!-- v-if="!getSearch('wm_forbid_jump')" -->
  <VanCountDown
    v-if="false"
    v-show="false"
    :time="time"
    @finish="countFinish"></VanCountDown>
</template>
<script>
import { getSearch } from '@/utils/index'

export default {
  props: {
    time: {
      type: Number,
      default: 30 * 60 * 1000
    },
    pName: {
      type: String,
      default: 'ZeroLive199'
    }
  },
  methods: {
    getSearch,
    countFinish () {
      this.$router.push({
        name: this.pName,
        query: {
          ...this.$route.query
        }
      })
    }
  }
}
</script>
